body{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15px;
}

input{
  width: 100%;
  font-size: 1.3rem;
  color: gray;
}


.card-container {
  display: flex;
  flex-wrap: wrap;
}

.card-container .card {
  border: solid 1px #f2f2f2;
  border-radius: 10px;
  margin: 5px;
}

.card-container .card .card-body {
  text-transform: uppercase;
  padding: 10px;
}

.card-container .card img.card-img {
  height: 300px;
  width: 300px;
  object-fit: cover;
}